import {echo} from "../golbal/commons";
import {act} from "../golbal/static";


Vue.component('delivery-management',{
    props: {

    },
    delimiters: ['${{', '}}'],
    template: ` <div> <h2-title title=""></h2-title>
     <div class="layui-collapse">
                    <div class="layui-colla-item">
                        <h2 class="layui-colla-title"></h2>
                        <div class="layui-colla-content layui-show">
                            <div class="layui-form" lay-filter="delivery-management-form">
                                <div class="layui-inline">   <input type="text" name="orderSn" placeholder="" autocomplete="off" class="layui-input"></div>
                                <div class="layui-inline">   <input type="text" name="SplitOrderSn" placeholder="" autocomplete="off" class="layui-input"></div>
                                <div class="layui-inline">  <input type="text" name="phone" placeholder="" autocomplete="off" class="layui-input"></div>
                                <div class="layui-inline">  <select name="region" >
                                    <option value=""></option>
                                    <option v-for="a in add" :value="a.name" v-text="a.name"></option>
                            
                                </select></div>
                                <div class="layui-inline">   <select name="orderStatus" >
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value="pendingDelivery"></option>
                                    <option value="delivered"></option>
                                    <option value="complete"></option>
                                    <option value="refused"></option>
                            
                                </select></div>
                                <div class="layui-inline"><button class="layui-btn" lay-submit  lay-filter="delivery-management-form-submit"></button></div>
                           
                            </div>
                        </div>
                <table class="layui-hide" id="delivery-management-table" lay-filter="delivery-management-table" ></table>
 
               </div>
        </div>
     
        <script type="text/html" id="delivery-management-table-bar">
    {{# if(d.orderStatus == ''||d.orderStatus == ''||d.orderStatus == ''){ }}
    <a class="layui-btn layui-btn-xs"  lay-event="waybill-number"></a>
    {{# } }}

    <a class="layui-btn layui-btn-xs site-active-edit"  data-clone="delivery-management-detail-clone" bar-type="delivery_management_detail" lay-event="edit" btn-type="edit">/</a>

    {{# if(d.orderStatus == ''){ }}
    <a class="layui-btn layui-btn-xs"  lay-event="cancel"></a>
    {{# } }}
    {{# if(d.orderStatus == ''){ }}
    <a class="layui-btn layui-btn-xs"  lay-event="takingOrder"></a>
    {{# } }}
    {{# if(d.orderStatus == ''){ }}
    <a class="layui-btn layui-btn-xs"  lay-event="replaceOrder"></a>
    {{# } }}
    {{# if(d.orderStatus == ''){ }}
    <a class="layui-btn layui-btn-xs"  lay-event="confirm"></a>
    {{# } }}
    {{# if(d.orderStatus == ''){ }}
    <a class="layui-btn layui-btn-xs"  lay-event="nogoods"></a>
    {{# } }}
    {{# if(d.orderStatus == ''||d.orderStatus == ''||d.orderStatus == ''||d.orderStatus == ''||d.orderStatus == ''||d.orderStatus == ''){ }}
    <a class="layui-btn layui-btn-xs site-active-edit" data-clone="distributive-buyer-clone" bar-type="distributive-buyer"  lay-event="distributive-buyer"></a>
    {{# } }}
</script>
     </div>
    `,
    data(){
        return {
            add: []
        }
    },
    mounted:function () {
        echo().data({
            act:act.reConfig_getRegionList
        }).succ(d=>{
            if(d)
              this.add=d
        }).post()
    },
    updated:function(){
        layui.use(['form'], function () {
            layui.form.render(null,'delivery-management-form')
        } )
    }
})